import './styles/transition.css'

import React, {Component} from "react";
import {HashRouter as Router, Route, useLocation, Switch} from 'react-router-dom';
import {TransitionGroup, CSSTransition} from "react-transition-group";

import Header from './Components/Header'
import Footer from './Components/Footer'
import Sang from './Components/Sang'
import Works from './Components/Works'
import About from './Components/About'

class App extends Component{
  render(){
    return(
       <Router>
         <Header/>
         <Section/>
         <Footer/>
       </Router>
  	)
  }
}
export default App

const Section = ()=>{
  let location = useLocation()
  const scrollToping = ()=>{
    window.scrollTo(0,0)
  }
  return(
    <TransitionGroup>
      <CSSTransition classNames='fade' exit={false} timeout={300} key={location.pathname}>
        <Switch>
          <Route exact path="/" onClick={scrollToping()} component={Sang}/>
          <Route path="/works" onClick={scrollToping()} component={Works}/>
          <Route path="/about" onClick={scrollToping()} component={About}/>
        </Switch>
      </CSSTransition>
    </TransitionGroup>
  )
}
