import React, {Component} from "react";

class Sec1 extends Component{
	constructor(props){
		super(props)
		this.state = {
			appear : false
		}
	}
	componentDidMount(){
		this.timer = setTimeout(()=>{
			this.setState({appear:true})
		})
	}
	componentWillUnmount(){
		clearTimeout(this.timer)
	}
	render(){
		const className = (this.state.appear===true) ? "appearActive" : "appear"
		return(
			 <section id="sec1">
				 <video autoPlay muted loop>
			 		 <source type="video/mp4" src="sources/videos/title.mp4"/>
			 	 </video>

			 	 <div className={["textwrap", className].join(' ')}>
			 		 <h1>Hello !</h1>
			 		 <p className="korean">
			 			 시각디자인을 전공하고 Design, Publishing, Front Engineering 를 목적으로
			 			 공부를 하고 있으며, 더욱 세련된 디자인과, 편리한 서비스를 추구합니다.
			 		 </p>
			 	 </div>
			 </section>
		)
	}

}

export default Sec1
