import React, {Fragment, Component} from "react";
import {NavLink} from "react-router-dom"

import '../styles/Header.css'

class Header extends Component{
	constructor(props){
		super(props)
		this.state = {
			navCheck : false
		}
	}
	TopButton(){
		window.scrollTo(0,0)
	}
	NavOn(){
		this.setState({navCheck : true})
	}
	NavOff(){
		this.setState({navCheck : false})
	}
	render(){
		let style = {
			transform:"translate(100%,0)"
		}
		if(this.state.navCheck===true){
			style.transform = "translate(0%,0)"
		}else {
			style.transform = "translate(100%,0)"
		}

		if(window.innerWidth>=768){
			return(
					<Fragment>
					 <header>
			       <ul>
			         <li><NavLink exact to='./' className='navItem' activeClassName="active">SANG</NavLink></li>
			         <li><NavLink to='./works' className='navItem' activeClassName="active">Works</NavLink></li>
			         <li><NavLink to='./about' className='navItem' activeClassName="active">About</NavLink></li>
			         <li><a target="_blank" className='navItem' rel="noopener noreferrer" href="https://blog.naver.com/zabefofoon">Blog</a></li>
			       </ul>
			     </header>
					 <div id="TopButton" onClick={()=>{this.TopButton()}}>▲</div>
				 </Fragment>
			)
		}
		else{
			return(
				<Fragment>
					<header>
						<img id="menu" alt="menu" src="sources/imgs/ham.svg" onClick={(e)=>this.NavOn()}/>
					</header>
					<ul className="mobileNav" style={style}>
						<img id="menu" alt="menu" src="sources/imgs/esc.svg" onClick={(e)=>this.NavOff()}/>
						<div className="liwrap">
							<li><NavLink exact to='./' className='navItem' activeClassName="active" onClick={(e)=>this.NavOff()}>SANG</NavLink></li>
							<li><NavLink to='./works' className='navItem' activeClassName="active" onClick={(e)=>this.NavOff()}>Works</NavLink></li>
							<li><NavLink to='./about' className='navItem' activeClassName="active" onClick={(e)=>this.NavOff()}>About</NavLink></li>
							<li><a target="_blank" className='navItem' rel="noopener noreferrer" href="https://blog.naver.com/zabefofoon" onClick={(e)=>this.NavOff()}>Blog</a></li>
						</div>
				</ul>
				</Fragment>
			)
		}

	}

}

export default Header
