import React,{Component} from "react";
import {Link} from "react-router-dom"
import '../styles/sec2.css'

class Sec2 extends Component {
	constructor(props){
		super(props)
		this.li = React.createRef();
		this.state = {
			clickIndex : 0,
			sliderItem : [
				{
					id : 0,
					src : "sources/imgs/slider1.png",
					title : "P01. Astro"
				},
				{
					id : 1,
					src : "sources/imgs/slider2.png",
					title : "P02. Weather"
				},
				{
					id : 2,
					src : "sources/imgs/slider3.png",
					title : "P03. Relative"
				},
				{
					id : 3,
					src : "sources/imgs/slider4.png",
					title : "P04. Portfolio"
				},
				{
					id : 4,
					src : "sources/imgs/slider5.png",
					title : "P05. FreeLance"
				},
				{
					id : 5,
					src : "sources/imgs/slider6.png",
					title : "P06. FreeLance2"
				},
				{
					id : 6,
					src : "sources/imgs/slider7.png",
					title : "P07. Chilsung"
				},
				{
					id : 7,
					src : "sources/imgs/slider8.png",
					title : "P08. Digital Graphic"
				},
			],
			sliderHeight : 0,
			overCheck : 0
		}
	}
	click(e){
		this.setState({
			clickIndex : Number(e.target.dataset.id)
		})
	}

	componentDidMount(){
		let intervalClick = 0
		this.ImgTimer = setInterval(()=>{
			this.setState({
				sliderHeight : this.li.offsetHeight
			})
		})
		this.Buttontimer = setInterval(()=>{
			intervalClick += 1
			if(intervalClick>=this.state.sliderItem.length)intervalClick = 0
			this.setState({
				clickIndex : intervalClick
			})
		},10000)
	}

	componentWillUnmount() {
		clearInterval(this.ImgTimer);
    clearInterval(this.Buttontimer);
  }
	mouseOver(){
		this.setState({
			overCheck : 1
		})
	}
	mouseOut(){
		this.setState({
			overCheck : 0
		})
	}
	render(){
		const ulHeight = {
			height : this.state.sliderHeight
		}
		const desStyle = {
			opacity : this.state.overCheck
		}
		const sliderItems = this.state.sliderItem.map((data)=>{
			const className =  (data.id===this.state.clickIndex) ? 'imgActive' : 'img';
			return(
				<Link to="works" key={data.id}>
					<li className={className} onMouseOver={(e)=>this.mouseOver()} onMouseOut={(e)=>this.mouseOut()}>
						<img ref={(ref)=>{this.li = ref}} src={data.src} alt={data.id}/>
						<p style={desStyle} className="description">{data.title}</p>
					</li>
				</Link>
			)
		})

		const buttonItems = this.state.sliderItem.map((data)=>{
			const className = (data.id===this.state.clickIndex) ? 'buttonItemActive' : ''
			return(
				<div key={data.id} data-id={data.id} className={["buttonItem", className].join(' ')} onClick={(e)=>this.click(e)}></div>
			)
		})

		return (
			<section id="sec2">
				<div className = "slider">
					<h2>Works</h2>
					<ul style={ulHeight}>
						{sliderItems}
					</ul>
					<div className="buttons">
						{buttonItems}
					</div>
				</div>
			</section>
		)
	}
}

export default Sec2
